import React, { Component } from 'react';

import { dialogPropTypes, dialogDefaultProps } from '../props';
import { modalOpenOperations, modalCloseOperations } from './DOMOperations';

export default class ModalDialog extends Component {
  static propTypes = dialogPropTypes;
  static defaultProps = dialogDefaultProps;

  componentDidMount() {
    modalOpenOperations();
  }

  componentWillUnmount() {
    modalCloseOperations();
  }

  render() {
    const { title, body, footer, onClose } = this.props;

    return (
      <div className="modal fade" style={{ display: 'block' }}>
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header">
              <button type="button" onClick={() => onClose()} className="close" aria-label="close">
                <span aria-hidden="true">&times;</span>
              </button>
              <h4 className="modal-title">{title}</h4>
            </div>
            <div className="modal-body">{body}</div>
            {footer && <div className="modal-footer">{footer}</div>}
          </div>
        </div>
      </div>
    );
  }
}



